        <div id="container">
            
            <img id="header-img" src="@/assets/about-us/banner.gif"> <!-- GIF here -->
            <img id= "side-img" src="@/assets/asd.jpg">
            
            <div class="person-desc" v-for="desc in descs" :key="desc" :style="{ gridArea: 'person-desc' + desc.index }">
                <img src="@/assets/unknown.png">
                
                <div class="desc">
                    <p class="title">{{ desc.name }}</p>
                    <p>{{ desc.desc }}</p>
                </div>
                
                <button type="button">Contact me</button>
            </div>
            
            <img id="uncensored" src="@/assets/about-us/UNCENSORED.gif">
            
        </div>
    </div>
</template>

<script>
export default {
    name: 'gallery',
    
    data() {
        return {
            descs: [
                { name: 'Alexander', desc: 'CEO. Lead programmer and designer. Contact me if you have general questions.', index: 1 },
                { name: 'Axel', desc: 'Social media manager and sales person. Contact me if you general questions or looking for any colaborations.', index: 2 },
                { name: 'Vilmer D.', desc: '', index: 3 },
                { name: 'Vilmer R.', desc: '', index: 4 }
            ]
        }
    }
}
</script>

<style>

#container {
    width: 80%;
    
    display: grid;
    grid-template-areas: 
        "header-img header-img   header-img   header-img"
        "side-img   side-img     person-desc1 person-desc1"
        "side-img   side-img     person-desc2 person-desc2"
        "side-img   side-img     person-desc3 person-desc3"
        "side-img   side-img     person-desc4 person-desc4"
        "uncensored uncensored   uncensored   uncensored";
        
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 160px 120px 120px 120px 120px 160px;
    
    gap: 12px;
}

/* 
 * # # # # 
 * 
 */

/* Grid items */
#container > img {
    width: 100%;
    height: 100%;
    
    object-fit: cover;
    
    user-select: none;
    pointer-events: none;
}

#header-img {
    grid-area: header-img;
}

#side-img {
    grid-area: side-img;
}

.person-desc {
    position: relative;
    
    padding: 16px 138px 16px 12px;
    
    display: flex;
    flex-direction: row;
    gap: 12px;
    
    width: 100%;
    height: 100%;
    
    background-color: var(--main-color);
}

.person-desc > img {
    
    height: 48px;
    width: 48px;
    
    border-radius: 100%;
}

.person-desc > div {
    display: flex;
    flex-direction: column;
    
    gap: 6px;
}

.person-desc p {
    margin: 0;
}

.person-desc > div > .title {
    font-weight: bold;
}

.person-desc > button {
    position: absolute;
    right: 12px;
    bottom: 12px;
    
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    
    border: none;
    outline: none;
    
    padding: 8px 16px;
    
    background-color: var(--button-color);
    color: var(--main-color);
    
    cursor: pointer;
}

#uncensored {
    grid-area: uncensored;
}

@media (orientation: portrait) {
