        grid-template-columns: 1fr 1fr;   
        grid-template-rows: 160px 150px 150px 150px 150px 80px;
    }
    
    #side-img {
        display: none;
    }
    
    .person-desc {
        padding: 16px 12px 48px 12px;
    }
}

</style>